<template>
  <div class="mdc-card andi-card">
    <div class="mdc-card__media mdc-card__media--square andi-image" :style="{ 'background-image': 'url('+gapiAndi.thumbnailPhotoUrl+')' }">
    </div>
    <div class="andi-textblock">
      <h2>{{ gapiAndi.name.fullName }}</h2>
      <p>{{andi.relation}}</p>
    </div>
  </div>
  <!-- <p>{{ andi.relation }}</p>
    <p v-if="!gapiAndi">{{ andi.email }}</p>
    <div v-else>
      <p>{{ gapiAndi.name.fullName }}</p>
      <div class="ANDi">
        <img class="thumbnail" :src="gapiAndi.thumbnailPhotoUrl">
      </div>
  </div>-->
</template>

<script>
export default {
  props: {
    andi: Object
  },
  data() {
    return {
      gapiAndi: null
    };
  },
  created() {
    this.$getGapiClient().then(gapi => {
      gapi.client.directory.users
        .list({
          customer: "my_customer",
          viewType: "domain_public",
          query: this.andi.email
        })
        .then(data => {
          this.gapiAndi = data.result.users[0];
        });
    });
  }
};
</script>

